.searchPage {
	flex: 0.8;
	padding: 10px 20px;
	background-color: #f9f9f9;
}

.searchPage_filter {
	display: flex;
	align-items: center;
	color: #606060;
	font-size: xx-small !important;
}

.searchPage_filter > h3 {
	margin-left: 10px;
	font-size: 14px;
}

.searchPage > hr {
	height: 1px;
	border: 0;
	background-color: lightgray;
	margin-top: 10px;
	margin-bottom: 10px;
}
